.collapse .title {
  padding: 5px 10px;
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  color: #c3c3c3;
  background: #2b2e3d;
  cursor: pointer; }
  .collapse .title .arrow {
    border: solid #c3c3c3;
    border-width: 0 2px 2px 0;
    display: inline-block;
    padding: 3px;
    margin: 1px 0;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    transition: transform .5s ease; }
    .collapse .title .arrow.down {
      transform: rotate(45deg) translateY(-3px);
      -webkit-transform: rotate(45deg) translateY(-3px); }

.collapse .controls {
  padding: 5px 10px; }

.cw-toolbar {
  display: block;
  width: 220px;
  min-height: 300px;
  background: #354057;
  color: #efefef;
  overflow: auto; }
  .cw-toolbar .tools {
    display: grid;
    grid-template-columns: fit-content(50%) fit-content(50%);
    grid-gap: 5px; }
    .cw-toolbar .tools .tool-item {
      cursor: -webkit-grab;
      cursor: grab; }
      .cw-toolbar .tools .tool-item svg {
        display: block;
        width: 100%;
        height: auto; }

.textarea-wrap {
  display: grid; }

.textarea-wrap::after {
  content: attr(data-replicated-value) " ";
  white-space: pre-wrap;
  visibility: hidden; }

.textarea-wrap > textarea {
  resize: none;
  overflow: hidden; }

.textarea-wrap > textarea,
.textarea-wrap::after {
  border: 1px solid black;
  padding: 0.5rem;
  font: inherit;
  grid-area: 1 / 1 / 2 / 2; }

.color-picker {
  display: inline-block; }
  .color-picker .color-box {
    display: inline-block;
    padding: 3px;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1);
    inset: 0px;
    cursor: pointer; }
    .color-picker .color-box .color-fill {
      width: 30px;
      height: 30px;
      background: #354057;
      border-radius: 2px; }
  .color-picker .popup {
    position: absolute;
    z-index: 2; }
    .color-picker .popup .popup-cover {
      position: fixed;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px; }
    .color-picker .popup.hide {
      display: none; }

.rc-slider .rc-slider-track {
  background-color: #20e2ae; }

.rc-slider .rc-slider-handle {
  border-color: #20e2ae;
  background: #20e2ae;
  width: 10px;
  height: 10px;
  margin-top: -3px; }
  .rc-slider .rc-slider-handle:active {
    width: 12px;
    height: 12px;
    margin-top: -4px;
    box-shadow: none; }

.rc-slider .rc-slider-tooltip .rc-slider-tooltip-inner {
  box-shadow: none; }

.cw-settings {
  display: block;
  width: 240px;
  min-height: 300px;
  background: #354057;
  color: #efefef;
  overflow: auto; }
  .cw-settings .form-group {
    display: flex; }
    .cw-settings .form-group .form-control {
      display: flex;
      flex-direction: column;
      flex: 1 1;
      font-size: 14px;
      margin-bottom: 5px; }
      .cw-settings .form-group .form-control.inline {
        flex-direction: row;
        align-items: center; }
      .cw-settings .form-group .form-control label {
        font-size: 13px;
        margin: 5px 0; }
      .cw-settings .form-group .form-control > input, .cw-settings .form-group .form-control textarea, .cw-settings .form-group .form-control select {
        display: block;
        padding: 0.4rem 0.3rem;
        font: inherit; }
      .cw-settings .form-group .form-control .rc-slider {
        margin-bottom: 5px; }

